'use strict';

import { Validate } from '@microsoft/sp-client-base';

/**
 * Context for all client side components
 */
export default class ClientSideComponentContext<TManifest> {

  private _domElement: HTMLElement;
  private _manifest: TManifest;

  /**
   * @internal
   */
  constructor(
    domElement: HTMLElement,
    manifest: TManifest) {

    Validate.isNotNullOrUndefined(domElement, 'domElement');
    Validate.isNotNullOrUndefined(manifest, 'manifest');

    this._domElement = domElement;
    this._manifest = manifest;
  }

  /**
   * Reference to the DOM element that hosts this client side component.
   */
  public get domElement(): HTMLElement { return this._domElement; }

  /**
   * Client side component manifest
   */
  public get manifest(): TManifest { return this._manifest; }
}